*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body{
  font-family: 'montseratt', sans-serif;
}

.app{
  background-image: url(./assets/cold-bg.jpg);
  background-size: cover;
  background-position: bottom;
  transition: 0.4 ease-out;
}

.app.warm{
  background-image: url(./assets/warm-bg.jpg);
}

main{
  min-height: 100vh;
  background-image: linear-gradient(to bottom, rgba(0,0,0,0.2), rgba(0,0,0,0.75));
  padding: 25px;
}

.search-box{
  width: 100%;
  margin: 0 0 75px;
}

.search-box .search-bar{
  display: block;
  width: 100%;
  padding: 15px;

  appearance: none;
  background: none;
  outline: none;
  border: none;

  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 0px 0px 16px 16px;
  margin-top: -25px;

  box-shadow: 0px 5px rgba(0,0,0,0.2);

  color: #313131;
  font-size: 20px;

  transition: 0.4s ease;
}

.search-box .search-bar:focus {
  background-color: rgba(255,255, 255,0.75); 
}

.location-box .location{
  color: #FFF;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

.location-box .date{
  color: #FFF;
  font-size: 20px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}

.weather-box{
  text-align: center;
}

.weather-box .temp{
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 16px;

  padding: 15px 25px;

  color: #fff;
  font-size: 102px;
  font-weight: 900;

  text-shadow: 3px 6px rgba(50,50,70,0.5);
  text-align: center;

  box-shadow: 3px 6px rgba(0,0,0,0.2);
}

.weather{
  
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: start;


  color: white;
  margin: 5px auto;
  padding: 15px 25px;
  font-size: 30px;
  font-weight: 700;
  text-shadow: 3px 3px rgba(50,50,70,0.5);
 
  border-radius: 16px;
  box-shadow: 3px 6px rgba(0,0,0,0.2);
  width: 100%;
  
}

.weather-box .weather .weather-detail {
  display: flex;
  align-items: center;
  text-shadow: 3px 3px rgba(50,50,70,0.5);
  padding: 6px 10px;
  width: 100%;
}

.weather-box .weather .weather-description {
  display: flex;
  text-shadow: 3px 3px rgba(50,50,70,0.5);
  align-items: center;
  font-size: 20px;
  padding: 6px 10px;
  width: 100%;


}

img{
  background-color: rgba(255, 255, 255, 0.2);
  font-size: 30px;
  padding: 5px;
  border-radius: 100%;
  margin-left: 20px;
}

span{
  color: rgba(255, 255, 255, 0.8);
  padding: 2px 4px;
}











